import {getAuth, signInWithEmailAndPassword, signOut, createUserWithEmailAndPassword} from 'firebase/auth'
import {getDatabase, ref, set} from 'firebase/database'

function getId() {
	return new Date().getTime()
}

export default {
	signIn(context, userInfo) {
		const auth = getAuth()
		const database = getDatabase()

		const uid = createUserWithEmailAndPassword(auth, userInfo.login, userInfo.password)
			.then((res) => {
				console.log(res.user, res)
				userInfo.accessToken = res.user.accessToken
				set(ref(database, `/users/${res.user.uid}`), userInfo)
			})
	},
	logOut(context, state) {
		const auth = getAuth()
		signOut(auth)
	},
	logIn(context, user) {
		const auth = getAuth();

		signInWithEmailAndPassword(auth, user.login, user.password)
			.then((userCredential) => {
				// Signed in 
				const user = userCredential.user
				context.commit('logIn', user)
				// ...
			})
			.catch((error) => {
				const errorCode = error.code;
				const errorMessage = error.message;
				console.warn(errorCode, errorMessage)
			});
	},
	setUser(context, user) {
		context.commit('setUser', user || {})
	}
}